






















































































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';

@Component({
  components: {Tabs},
})
export default class Statistics extends Vue {
  dateTypeList = [
    {text: '按天', value: 'day'},
    {text: '按月', value: 'month'},
  ];
  date = 'day';

  Hash = [
    {id: 0, day: '星期天'},
    {id: 1, day: '星期一'},
    {id: 2, day: '星期二'},
    {id: 3, day: '星期三'},
    {id: 4, day: '星期四'},
    {id: 5, day: '星期五'},
    {id: 6, day: '星期六'},
  ];

  // eslint-disable-next-line no-undef
  tagNameString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join('，');
  }

  // eslint-disable-next-line no-undef
  tagIconNameString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.iconName).join('，');
  }

  dayBeautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    const index = day.day();
    const x = this.Hash.filter(item => item.id === index)[0].day;
    if (day.isSame(now, 'day')) {
      return `今天   ${x}`;
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return `昨天   ${x}`;
    } else if (day.isSame((now), 'year')) {
      return day.format('M月D日');
    } else {
      return day.format('YYYY年M月D日');
    }
  }

  monthBeautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'month')) {
      return '本月';
    } else if (day.isSame((now), 'year')) {
      return day.format('M月');
    } else {
      return day.format('YYYY年M月');
    }
  }

  get recordList() {
    return this.$store.state.recordList;
  }

  get groupedDayList() {
    const {recordList} = this;

    const newList = clone(recordList).filter((r: RecordItem) => r.type === this.type).sort((a: RecordItem, b: RecordItem) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {return [] as Result;}
    type Result = { title: string, total?: number, items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  get groupedMonthList() {
    const {recordList} = this;

    const newList = clone(recordList).filter((r: RecordItem) => r.type === this.type).sort((a: RecordItem, b: RecordItem) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {return [] as Result;}
    type Result = { title: string, total?: number, items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'month')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  Created() {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  recordTypeList = recordTypeList;

  go(id: string) {
    this.$router.replace(`/record/${id}`);
  }
}
