

















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import Chart from '@/components/Chart.vue';
import clone from '@/lib/clone';
import dayjs from 'dayjs';
import _ from 'lodash';

@Component({
  components: {Tabs, Chart},
})
export default class Statistics extends Vue {
  get chartOptions() {
    const keys = this.keyValueList.map(item => item.key);
    const values = this.keyValueList.map(item => item.value);
    this.total = values.reduce((sum, item) => sum + item, 0);
    return {
      grid: {
        top: '5%',
        bottom: '30%',
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {alignWithLabel: true},
        axisLine: {lineStyle: {color: '#666'}},
        axisLabel: {
          formatter: function (value: string, index: number) {
            return value.substr(5);
          }
        }
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        symbol: 'emptyCircle',
        symbolSize: 6,
        itemStyle: {borderWidth: 1, color: '#666', borderColor: '#666'},
        lineStyle: {width: 1},
        data: values,
        type: 'line'
      }],
      tooltip: {
        show: true, triggerOn: 'click',
        position: 'top',
        formatter: '{c}'
      }
    };
  }

  get keyValueList() {
    if (this.date === 'day') {
      const today = new Date();
      const array = [];
      const date = new Date();
      //当前月份的天数
      date.setMonth(date.getMonth() + 1);
      date.setDate(0);
      const days = date.getDate();
      //当前月份的第一天
      const firstDate = new Date();
      firstDate.setDate(0);
      for (let i = 1; i <= days; i++) {
        // this.recordList = [{date:7.3, value:100}, {date:7.2, value:200}]
        const dateString = dayjs(firstDate)
            .add(i, 'day').format('YYYY-MM-DD');
        const found = _.find(this.groupedList, {
          title: dateString
        });
        array.push({
          key: dateString, value: found ? found.total : 0
        });
      }
      return array;
    } else {
      const array = [];
      //当前年份的第一个月
      const firstMonth = new Date();
      firstMonth.setMonth(0);
      for (let i = 0; i <= 11; i++) {
        const dateString = dayjs(firstMonth)
            .add(i, 'month').format('YYYY-MM');
        const found = _.find(this.groupedList, {
          title: dateString
        });
        array.push({
          key: dateString, value: found ? found.total : 0
        });
      }
      return array;
    }
  }

  get groupedList() {
    if (this.date === 'day') {
      const {recordList} = this;
      const newList = clone(recordList).filter((r: RecordItem) => r.type === this.type).sort((a: RecordItem, b: RecordItem) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
      if (newList.length === 0) {return [] as Result;}
      type Result = { title: string, total: number, items: RecordItem[] }[]
      const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), total: 0, items: [newList[0]]}];
      for (let i = 1; i < newList.length; i++) {
        const current = newList[i];
        const last = result[result.length - 1];
        if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
          last.items.push(current);
        } else {
          result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), total: 0, items: [current]});
        }
      }
      result.map(group => {
        group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
      });
      return result;
    } else {
      const {recordList} = this;
      const newList = clone(recordList).filter((r: RecordItem) => r.type === this.type).sort((a: RecordItem, b: RecordItem) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
      if (newList.length === 0) {return [] as Result;}
      type Result = { title: string, total: number, items: RecordItem[] }[]
      const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM'), total: 0, items: [newList[0]]}];
      for (let i = 1; i < newList.length; i++) {
        const current = newList[i];
        const last = result[result.length - 1];
        if (dayjs(last.title).isSame(dayjs(current.createdAt), 'month')) {
          last.items.push(current);
        } else {
          result.push({title: dayjs(current.createdAt).format('YYYY-MM'), total: 0, items: [current]});
        }
      }
      result.map(group => {
        group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
      });
      return result;
    }
  }


  dateTypeList = [
    {text: '按天', value: 'day'},
    {text: '按月', value: 'month'},
  ];
  date = 'day';

  // eslint-disable-next-line no-undef
  tagNameString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join('，');
  }

  // eslint-disable-next-line no-undef
  tagIconNameString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.iconName).join('，');
  }

  get recordList() {
    return this.$store.state.recordList;
  }

  Created() {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  recordTypeList = recordTypeList;

  total = 0;

}
